export default {
  name: 'keyboard',
  data () {
    return {
      type: 0,
      chineseList: [
        '京', '津', '渝', '沪', '冀', '晋', '辽', '吉', '黑', '苏',
        '浙', '皖', '闽', '赣', '鲁', '豫', '鄂', '湘', '粤', '琼',
        '川', '贵', '云', '陕', '甘', '青', '蒙', '桂', '宁', '新',
        '藏', '使', '领', '警', '学', '港', '澳'
      ],
      letterList: [
        1, 2, 3, 4, 5, 6, 7, 8, 9, 0,
        'Q', 'W', 'E', 'R', 'T', 'Y', 'U', 'I', 'O', 'P',
        'A', 'S', 'D', 'F', 'G', 'H', 'J', 'K', 'L',
        'Z', 'X', 'C', 'V', 'B', 'N', 'M'
      ],
      highlight: false,
      currentIndex: '',
      licence: '',
      finished: false
    }
  },
  props: {
    visible: false,
    electric: false
  },
  methods: {
    touchStart (index) {
      if (this.finished) {
        return false
      }
      if (this.electric === false && this.licence.length >= 7) {
        return false
      } else if (this.electric && this.licence.length >= 8) {
        return false
      }
      this.currentIndex = index
      this.highlight = true
    },

    touchEnd (index, type) {
      if (this.finished) {
        return false
      }
      if ((this.electric === false && this.licence.length >= 7) || (this.electric && this.licence.length >= 8)) {
        return false
      }
      this.highlight = false
      if (type === 0) {
        this.licence += this.chineseList[this.currentIndex].toString()
      } else if (type === 1) {
        this.licence += this.letterList[this.currentIndex].toString()
      }

      if ((this.electric === false && this.licence.length === 7) || (this.electric && this.licence.length === 8)) {
        this.$emit('licenceFinished')
      }

      this.$emit('licenceCompleted', this.licence)
    },
    switchType () {
      this.type = this.type === 0 ? 1 : 0
    },
    rollBack () {
      if (this.finished) {
        return false
      }
      let licence = this.licence
      licence = licence.substr(0, licence.length - 1)
      this.licence = licence
      this.$emit('licenceCompleted', this.licence)
    }
  }
}



